                        '/assets/defaults/ceramonies/images/' +\n                                item.imageOne\n                            \"\n                            alt=\"\"\n                        />\n                    </div>\n                    <div class=\"cere-view-info\">\n                        <h3>{{ titleCase(item.name) }}</h3>\n                        <div class=\"cere-view-info-time\">\n                            <div>\n                                <img\n                                    src=\"/assets/ceramonies/calendar.svg\"\n                                    alt=\"\"\n                                />\n                            </div>\n                            <div>\n                                <div>\n                                    {{\n                                        new Date(\n                                            item.startDate\n                                        ).toLocaleDateString(\"en-us\", {\n                                            weekday: \"long\",\n                                            year: \"numeric\",\n                                            month: \"long\",\n                                            day: \"numeric\",\n                                        })\n                                    }}\n                                </div>\n                                <div>\n                                    {{\n                                        new Date(\n                                            \"1970-01-01T\" + item.startTime + \"Z\"\n                                        ).toLocaleTimeString(\"en-US\", {\n                                            timeZone: \"UTC\",\n                                            hour12: true,\n                                            hour: \"numeric\",\n                                            minute: \"numeric\",\n                                        })\n                                    }}\n                                </div>\n                            </div>\n                        </div>\n                        <div class=\"cere-view-info-time\">\n                            <div>\n                                <img\n                                    src=\"/assets/ceramonies/map-pin.svg\"\n                                    alt=\"\"\n                                />\n                            </div>\n                            <div>\n                                <div v-for=\"e in item.venues\" :key=\"e.name\">\n                                    {{ e.name }}, {{ e.location.name }},\n                                    {{ e.location.state }}\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n            </div>\n            <div\n                v-if=\"popupCera\"\n                class=\"cere-popup-container\"\n                @click=\"popupCera = null\"\n            >\n                <component\n                    @ceraClose=\"popupCera = null\"\n                    :ceremony=\"popupCera\"\n                    :brideName=\"invitation.brideName\"\n                    :groomName=\"invitation.groomName\"\n                    v-bind:is=\"ceremonyCard\"\n                ></component>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport CardDefault from \"./ceremonyCards/CardDefault.vue\";\nimport CardMehendi from \"./ceremonyCards/CardMehendi.vue\";\nimport CardRoka from \"./ceremonyCards/CardRoka.vue\";\nimport CardEngagement from \"./ceremonyCards/CardEngagement.vue\";\nimport CardSangeet from \"./ceremonyCards/CardSangeet.vue\";\nimport CardHaldi from \"./ceremonyCards/CardHaldi.vue\";\nimport CardReception from \"./ceremonyCards/CardReception.vue\";\nimport CardCustom from \"./ceremonyCards/CardCustom.vue\";\nimport CardTilak from \"./ceremonyCards/CardTilak.vue\";\nimport CardSagan from \"./ceremonyCards/CardSagan.vue\";\nimport CardWedding from \"./ceremonyCards/CardWedding.vue\";\n\nexport default {\n    components: {\n        CardDefault,\n        CardMehendi,\n        CardRoka,\n        CardEngagement,\n        CardSangeet,\n        CardHaldi,\n        CardReception,\n        CardCustom,\n        CardTilak,\n        CardSagan,\n        CardWedding,\n    },\n    props: [\"ceramonies\", \"invitation\"],\n    data() {\n        return {\n            popupCera: null,\n        };\n    },\n    methods: {\n        titleCase(str) {\n            let _this = this;\n            var splitStr = str.toLowerCase().split(\" \");\n            for (var i = 0; i < splitStr.length; i++) {\n                // You do not need to check if i is larger than splitStr length, as your for does that for you\n                // Assign it back to the array\n                splitStr[i] =\n                    splitStr[i].charAt(0).toUpperCase() +\n                    splitStr[i].substring(1);\n            }\n            // Directly return the joined string\n            return splitStr.join(\" \");\n        },\n    },\n    computed: {\n        ceremonyCard() {\n            return this.popupCera.file;\n        },\n    },\n};\n</script>\n\n<style scoped>\n.guest-invi-ceramony {\n    background-image: url(\"/assets/guestInvi/cera-back.png\");\n    background-repeat: no-repeat;\n    background-position: top left;\n    padding: 68px;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n.guest-invi-ceramony > h3 {\n    font-family: \"Perpetua\";\n    font-style: normal;\n    font-weight: 700;\n    font-size: 29.5136px;\n    line-height: 34px;\n    color: #333333;\n    margin-bottom: 15px;\n}\n.guest-invi-ceramony > h2 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 14.5337px;\n    line-height: 22px;\n    color: #000000;\n    margin-bottom: 15px;\n    text-align: center;\n}\n.cera-view-cont {\n    overflow-x: scroll;\n    -ms-overflow-style: none; /* IE and Edge */\n    scrollbar-width: none; /* Firefox */\n    display: flex;\n    width: 100%;\n    padding: 10px;\n}\n.cera-view-cont::-webkit-scrollbar {\n    display: none;\n}\n.cere-view-single {\n    background: #ffffff;\n    border: 0.575294px solid #9c9797;\n    border-radius: 8.05411px;\n    padding: 10px;\n    min-width: 230px;\n    margin-right: 15px;\n    max-width: 300px;\n    width: 100%;\n}\n.cere-single-image-cont {\n    position: relative;\n    padding-top: 80%;\n}\n.cere-single-image-cont > img {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n}\n.cere-single-image-cont > div {\n    position: absolute;\n    top: 15px;\n    right: 15px;\n    z-index: 99999;\n}\n.cere-view-info {\n    margin-top: 15px;\n}\n.cere-view-info > h3 {\n    font-family: \"Perpetua\";\n    font-style: normal;\n    font-weight: 700;\n    font-size: 18px;\n    line-height: 21px;\n    margin-bottom: 10px;\n    color: #461952;\n}\n.cere-view-info-time {\n    display: flex;\n    align-items: baseline;\n}\n.cere-view-info-time > div:nth-child(1) {\n    margin-right: 10px;\n}\n.cere-view-info-time > div:nth-child(2) {\n    font-family: \"Poppins\", sans-serif;\n    font-style: normal;\n    font-weight: 300;\n    font-size: 10.2px;\n    line-height: 15px;\n    color: #31373a;\n}\n.cere-popup-container {\n    background: rgba(38, 38, 38, 0.8);\n    position: fixed;\n    width: 100vw;\n    height: 100vh;\n    top: 0;\n    z-index: 999999;\n    display: flex;\n    justify-content: center;\n    overflow-y: auto;\n    -ms-overflow-style: none; /* IE and Edge */\n    scrollbar-width: none; /* Firefox */\n    padding: 10px;\n}\n.cere-popup-container::-webkit-scrollbar {\n    display: none;\n}\n@media screen and (max-width: 576px) {\n    .guest-invi-ceramony {\n        padding: 68px 21px;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
