\n        display: none;\n    }\n    .guest-helpline-single.active > span:nth-child(4),\n    .guest-helpline-single.active > span:nth-child(5) {\n        display: block !important;\n        margin-bottom: 5px;\n    }\n    .guest-helpline-single.active > span:nth-child(1) {\n        margin-bottom: 10px;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
