on);\n            window.scrollTo({\n                top: offsetPosition,\n                behavior: \"smooth\",\n            });\n        },\n        reactInvitation(type) {\n            let _this = this;\n            this.disableReact = true;\n            let link = route(\"guestaccepted\", [\n                _this.loggedIn,\n                _this.invitation,\n            ]);\n            if (type == \"decline\") {\n                link = route(\"guestdeclined\", [\n                    _this.loggedIn,\n                    _this.invitation,\n                ]);\n            }\n            axios({\n                method: \"POST\",\n                url: link,\n            })\n                .then((response) => {\n                    console.log(response);\n                    _this.disableReact = false;\n                    //_this.invitation = response.data;\n                    if (type == \"accept\") {\n                        _this.message = \"Invitation Accepted!!\";\n                        _this.invitation.guest_invitation.inviteStatus = 6;\n                    }\n                    if (type == \"decline\") {\n                        _this.message = \"Invitation Declined!!\";\n                        _this.invitation.guest_invitation.inviteStatus = 5;\n                    }\n                    console.log(_this.message);\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                })\n                .catch((error) => {\n                    console.log(error);\n                    _this.disableReact = false;\n                    _this.message = \"Something went wrong!!\";\n\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                });\n        },\n        countDown() {\n            const fixedDate = this.invitation.startDate;\n            //console.log(fixedDate);\n            const deadlineTimer = document.querySelectorAll(\n                \".guest-banner-timer-single > span:nth-child(1)\"\n            );\n\n            const futureDate = new Date(fixedDate);\n\n            const futureTime = futureDate.getTime();\n\n            function getRemainingTime() {\n                let today = new Date().getTime();\n\n                let totalRemainingMS = futureTime - today;\n                if (totalRemainingMS < 0) {\n                    clearInterval(countdown);\n                    return;\n                }\n\n                //values in ms\n                const Oneday = 24 * 60 * 60 * 1000;\n                const Onehour = 60 * 60 * 1000;\n                const Oneminutes = 60 * 1000;\n\n                //remaining time calulation\n                let Day = Math.floor(totalRemainingMS / Oneday);\n                let Hour = Math.floor((totalRemainingMS % Oneday) / Onehour);\n                let Minutes = Math.floor(\n                    (totalRemainingMS % Onehour) / Oneminutes\n                );\n                let Seconds = Math.floor(\n                    (totalRemainingMS % Oneminutes) / 1000\n                );\n\n                const value = [Day, Hour, Minutes, Seconds];\n\n                function format(item) {\n                    if (item < 10) {\n                        return (item = `0${item}`);\n                    } else {\n                        return item;\n                    }\n                }\n                deadlineTimer.forEach(function (item, index) {\n                    item.innerHTML = format(value[index]);\n                });\n            }\n            let countdown = setInterval(getRemainingTime, 1000);\n            getRemainingTime();\n        },\n        setWidth() {\n            this.screenWidth = window.screen.width;\n        },\n    },\n    mounted() {\n        window.addEventListener(\"resize\", this.setWidth);\n        this.$nextTick(() => {\n            this.countDown();\n            if (this.story) {\n                this.$refs.story.countDown();\n            }\n        });\n    },\n};\n</script>\n\n<style scoped>\n.hide {\n    max-height: 0 !important;\n    overflow: hidden !important;\n    padding: 0 !important;\n}\n/* Navbar css start */\n.guest-invi-navbar-cont {\n    position: fixed;\n    display: flex;\n    justify-content: center;\n    top: 91px;\n    z-index: 9999999999;\n    width: 100%;\n}\n.guest-invi-navbar {\n    background: #461952;\n    border-radius: 27px;\n    padding: 8px 13px;\n    display: flex;\n    justify-content: space-between;\n    overflow-x: auto;\n    -ms-overflow-style: none; /* IE and Edge */\n    scrollbar-width: none; /* Firefox */\n}\n.guest-invi-navbar::-webkit-scrollbar {\n    display: none;\n}\n.guest-invi-navbar > div,\n.guest-invi-navbar > a {\n    text-decoration: none;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 15px;\n    line-height: 22px;\n    text-align: center;\n    color: #9a9a9a;\n    padding: 8px 40px;\n    cursor: pointer;\n    text-decoration: none;\n    white-space: nowrap;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n.guest-invi-navbar > div > span,\n.guest-invi-navbar > a > span {\n    width: 46px;\n    height: 46px;\n    margin-bottom: 25px;\n    border-radius: 50%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    background: rgba(255, 255, 255, 0.1);\n}\n.guest-invi-navbar > .active {\n    background: #ffffff;\n    border-radius: 23px;\n    color: #461952;\n    font-weight: 700;\n}\n/* Navbar css end */\n\n/* Banner css start */\n.guest-banner-cont {\n    padding-top: 40%;\n    position: relative;\n    width: 100%;\n    height: 100%;\n    top: 0;\n}\n.guest-banner-cont > img:nth-child(1) {\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    top: 0;\n    object-fit: cover;\n}\n.guest-banner-cont > .overlay {\n    background: rgba(0, 0, 0, 0.5);\n    width: 