\n            // });\n            // conts.forEach(function (item) {\n            //     if (item.classList.contains(\"active\")) {\n            //         item.classList.remove(\"active\");\n            //     }\n            // });\n            // mainCont.css(\"display\", \"block\");\n            // currEle.classList.add(\"active\");\n            // currEle.nextElementSibling.classList.add(\"active\");\n        },\n        showAlbums(e) {\n            let all = document.getElementsByClassName(\"album-pics\");\n            let conts = document.getElementsByClassName(\"album-cont\");\n            let mainCont = $(\"#all-albums\");\n            all.forEach(function (item) {\n                if (item.classList.contains(\"active\")) {\n                    item.classList.remove(\"active\");\n                }\n            });\n            conts.forEach(function (item) {\n                if (item.classList.contains(\"active\")) {\n                    item.classList.remove(\"active\");\n                }\n            });\n            mainCont.css(\"display\", \"grid\");\n        },\n    },\n    computed: {\n        sixImages() {\n            let images = [];\n            let all = this.inviImages;\n            let index = 0;\n            if (all.length > 0) {\n                all.every((item) => {\n                    item[\"type\"] = \"img\";\n                    images.push(item);\n                    index++;\n                    if (index == 6) {\n                        return false;\n                    } else {\n                        return true;\n                    }\n                });\n            }\n            if (images.length < 6) {\n                let videos = this.inviVideos;\n                if (videos.length > 0) {\n                    videos.every((item) => {\n                        item[\"type\"] = \"vid\";\n                        images.push(item);\n                        index++;\n                        if (index == 6) {\n                            return false;\n                        } else {\n                            return true;\n                        }\n                    });\n                }\n            }\n            return images;\n        },\n    },\n};\n</script>\n\n<style scoped>\n.guest-invi-gallary-cont {\n    padding: 68px;\n    background: #ffffff;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n}\n.guest-invi-heading {\n    font-family: \"Perpetua\";\n    font-style: normal;\n    font-weight: 700;\n    font-size: 29.5136px;\n    line-height: 34px;\n    text-align: center;\n    color: #333333;\n    margin-bottom: 15px;\n}\n.guest-invi-gallary {\n    display: grid;\n    grid-template-columns: repeat(3, 1fr);\n    grid-template-rows: max-content;\n    grid-column-gap: 10px;\n    grid-row-gap: 20px;\n    align-items: flex-start;\n    background: #f8edf0;\n    width: 55%;\n    padding: 19px 25px;\n    margin: auto;\n}\n.guest-invi-gallary > div {\n    position: relative;\n    padding-top: 85%;\n}\n.guest-invi-gallary > div > img,\n.guest-invi-gallary > div > video {\n    position: absolute;\n    top: 0;\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n}\n.guest-invi-gallary-cont > button:nth-child(3) {\n    outline: none;\n    border: 1px solid #d8839f;\n    border-radius: 10px;\n    background: white;\n    padding: 12px 32px;\n    width: max-content;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 13px;\n    line-height: 20px;\n    text-align: center;\n    color: #c4456f;\n    margin-top: 46px;\n}\n@media screen and (max-width: 1140px) {\n    .guest-invi-gallary {\n        width: 80% !important;\n        /* grid-template-columns: repeat(auto-fill, minmax(182px, 1fr)); */\n    }\n}\n@media screen and (max-width: 910px) {\n    .guest-invi-gallary {\n        width: 100% !important;\n    }\n}\n@media screen and (max-width: 576px) {\n    .guest-invi-gallary-cont {\n        padding: 68px 15px;\n    }\n    .guest-invi-gallary {\n        padding: 8px;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
