r Family</span\n                    >\n                </p>\n                <div class=\"cere-reception-time-venue\">\n                    <h5>\n                        <img\n                            src=\"/assets/guestInvi/bi_calendar-fill.svg\"\n                            alt=\"\"\n                        />\n                        Ceremony:\n                    </h5>\n                    <p>\n                        <span>\n                            {{\n                                new Date(ceremony.startDate).toLocaleDateString(\n                                    \"en-us\",\n                                    {\n                                        weekday: \"long\",\n                                        year: \"numeric\",\n                                        month: \"long\",\n                                        day: \"numeric\",\n                                    }\n                                )\n                            }}\n                        </span>\n                        <span>\n                            From\n                            {{\n                                new Date(\n                                    \"1970-01-01T\" + ceremony.startTime + \"Z\"\n                                ).toLocaleTimeString(\"en-US\", {\n                                    timeZone: \"UTC\",\n                                    hour12: true,\n                                    hour: \"numeric\",\n                                    minute: \"numeric\",\n                                })\n                            }}\n                        </span>\n                    </p>\n                </div>\n                <div class=\"cere-reception-time-venue\">\n                    <h5>\n                        <img\n                            src=\"/assets/guestInvi/bi_location-fill.svg\"\n                            alt=\"\"\n                        />\n                        Venue:\n                    </h5>\n                    <p v-for=\"e in ceremony.venues\" :key=\"e.name\">\n                        {{ e.name }}, {{ e.location.name }},\n                        {{ e.location.state }}i\n                    </p>\n                </div>\n                <div\n                    v-if=\"\n                        ceremony.menDress ||\n                        ceremony.ladiesDress ||\n                        ceremony.imageTwo ||\n                        ceremony.imageThree\n                    \"\n                    class=\"cere-reception-dress-cont\"\n                >\n                    <h3>Dress Code</h3>\n                    <div>\n                        <div v-if=\"ceremony.menDress || ceremony.imageTwo\">\n                            <span>\n                                <img\n                                    src=\"/assets/ceramonies/carbon_gender-male.svg\"\n                                    alt=\"\"\n                                />\n                                <span>Men:</span>\n                            </span>\n                            <span v-if=\"ceremony.menDress\">\n                                {{ ceremony.menDress }}\n                            </span>\n                            <div v-if=\"ceremony.imageTwo\">\n                                <img\n                                    class=\"dress-img\"\n                                    :src=\"\n                                        '/assets/defaults/ceramonies/images/' +\n                                        ceremony.imageTwo\n                                    \"\n                                    alt=\"\"\n                                />\n                            </div>\n                        </div>\n                        <div v-if=\"ceremony.ladiesDress || ceremony.imageThree\">\n                            <span>\n                                <img\n                                    src=\"/assets/ceramonies/carbon_gender-female.svg\"\n                                    alt=\"\"\n                                />\n                                <span>Women:</span>\n                            </span>\n                            <span v-if=\"ceremony.ladiesDress\">\n                                {{ ceremony.ladiesDress }}\n                            </span>\n                            <div v-if=\"ceremony.imageThree\">\n                                <img\n                                    class=\"dress-img\"\n                                    :src=\"\n                                        '/assets/defaults/ceramonies/images/' +\n                                        ceremony.imageThree\n                                    \"\n                                    alt=\"\"\n                                />\n                            </div>\n                        </div>\n                    </div>\n                </div>\n                <div class=\"cere-reception-end\">\n                    With pride and joy, we invite you to share a special day in\n                    our lives as we\n                </div>\n            </div>\n            <img\n                class=\"top-right\"\n                src=\"/assets/guestInvi/reception-card-top-right.png\"\n                alt=\"\"\n            />\n            <img\n                class=\"bottom-left\"\n                src=\"/assets/guestInvi/reception-card-bottom-left.png\"\n                alt=\"\"\n            />\n            <img\n                class=\"bottom-right\"\n                src=\"/assets/guestInvi/reception-card-bottom-right.png\"\n                alt=\"\"\n            />\n        </div>\n        <button @click=\"$emit('ceraClose')\" type=\"button\">\n            <img\n                class=\"grey-filter\"\n                src=\"/assets/guestInvi/Group-1714.svg\"\n                alt=\"\"\n            />\n        </button>\n    </div>\n</template>\n\n<script>\nexport default {\n    props: [\"ceremony\", \"brideName\", \"groomName\"],\n    methods: {\n        titleCase(str) {\n            let _this = this;\n            var splitStr = str.toLowerCase().split(\" \");\n            for (var i = 0; i < splitStr.length; i++) {\n                // You do not need to check if i is larger than splitStr length, as your for does that for you\n                // Assign it back to the array\n                splitStr[i] =\n                    splitStr[i].charAt(0).toUpperCase() +\n                    splitStr[i].substring(1);\n            }\n            // Directly return the joined string\n            return splitStr.join(\" \");\n        },\n    },\n};\n</script>\n\n<style scoped>\n.cera-single-popup-cont {\n    background: #ffffff;\n    background-image: url(\"/assets/guestInvi/reception-card-back.png\");\n    border-radius: 8px;\n    width: 60%;\n    max-width: 512px;\n    position: relative;\n    z-index: 999999;\n    height: max-content;\n    margin: auto;\n    margin-top: 160px;\n}\n.cera-single-popup-cont > button {\n    position: absolute;\n    top: 18px;\n    right: 23px;\n    z-index: 9999;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 41px;\n    height: 41px;\n    background: rgba(255, 255, 255, 0.4);\n    border: 0.5px solid #e3d9d7;\n    backdrop-filter: blur(7.85714px);\n}\n.top-right {\n    position: absolute;\n    top: 0;\n    right: 0;\n    max-width: 145px;\n    width: 100%;\n    z-index: 99;\n}\n.bottom-left {\n    position: absolute;\n    bottom: 0;\n    left: 0;\n    max-width: 145px;\n    width: 100%;\n    z-index: 99;\n}\n.bottom-right {\n    position: absolute;\n    bottom: 0;\n    right: 0;\n    max-width: 185px;\n    width: 100%;\n    z-index: 99;\n}\n.cere-reception-cont {\n    position: relative;\n    padding: 40px 33px;\n    /* background-image: url(\"/assets/guestInvi/reception-card-top-right.png\"),\n        url(\"/assets/guestInvi/reception-card-bottom-left.png\"),\n        url(\"/assets/guestInvi/reception-card-bottom-right.png\");\n    background-repeat: no-repeat, no-repeat, no-repeat;\n    background-position: right top, left bottom, right bottom;\n    background-size: auto, auto, auto; */\n}\n.grey-filter {\n    filter: invert(80%) sepia(1%) saturate(0) hue-rotate(314deg) brightness(12%)\n        contrast(1%);\n}\n.cere-reception-cont-inner {\n    padding: 25px 39px;\n    background: #ffffff;\n    border: 10px solid #e29b43;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n}\n.cere-reception-cont-inner > * {\n    position: relative;\n    z-index: 999;\n}\n.cere-reception-couple {\n    font-family: \"Bell MT\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 14.1268px;\n    line-height: 16px;\n    text-align: center;\n    color: #000000;\n    margin-bottom: 10px;\n    text-transform: uppercase;\n}\n.cere-reception-title {\n    font-family: \"Engagement\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 46px;\n    line-height: 57px;\n    text-align: center;\n    color: #613f35;\n}\n.cere-reception-invite-type {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 300;\n    font-size: 13px;\n    line-height: 20px;\n    text-align: center;\n    color: #613f35;\n}\n.cere-reception-invite-type > .bold {\n    font-weight: 500;\n}\n.cere-reception-time-venue > h5 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 13.5989px;\n    line-height: 20px;\n    text-align: center;\n    color: #613f35;\n}\n.cere-reception-time-venue > p {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 300;\n    font-size: 13px;\n    line-height: 20px;\n    text-align: center;\n    color: #613f35;\n}\n.cere-reception-dress-cont {\n    width: 100%;\n}\n.cere-reception-dress-cont > h3 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 600;\n    font-size: 13.5989px;\n    line-height: 20px;\n    color: #613f35;\n    text-align: center;\n}\n.cere-reception-dress-cont > div {\n    display: flex;\n    justify-content: center;\n    width: 100%;\n}\n.cere-reception-dress-cont > div > div:nth-child(1) {\n    margin-right: 32px;\n}\n.dress-img {\n    width: 111px;\n    height: 73px;\n    margin-top: 8px;\n    border-radius: 2px;\n}\n.cere-reception-dress-cont {\n    margin-top: 13px;\n}\n\n.cere-reception-dress-cont > div {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 300;\n    font-size: 11.29px;\n    line-height: 17px;\n    color: #613f35;\n}\n.cere-reception-end {\n    font-family: \"Poppins\";\n    font-style: italic;\n    font-weight: 400;\n    font-size: 13px;\n    line-height: 20px;\n    text-align: center;\n    color: #613f35;\n    width: 80%;\n    margin-top: 15px;\n}\n@media screen and (max-width: 768px) {\n    .cera-single-popup-cont {\n        width: 95% !important;\n    }\n    /* .cere-roka-cont {\n        background-size: 40%, 40% !important;\n    } */\n}\n</style>\n"],"sourceRoot":""}]);
