g:12px 15.5px;text-align:center;width:-moz-max-content;width:max-content}.guest-acc-family-wait>button>img[data-v-35a24cc2],.guest-acc-room-head>div>button:first-child>img[data-v-35a24cc2]{margin-right:6px}.guest-acc-room-head[data-v-35a24cc2]{position:relative;width:100%}.guest-acc-room-head>div>button[data-v-35a24cc2]{background-color:#fff;border:1px solid #d8839f;border-radius:10px;color:#c4456f;font-family:Poppins;font-size:13px;font-style:normal;font-weight:500;line-height:20px;margin-left:15px;outline:none;padding:12px 15.5px;text-align:center;width:-moz-max-content;width:max-content}.guest-invi-room-head[data-v-35a24cc2],.guest-invi-room-single[data-v-35a24cc2]{border-bottom:1.1635px solid #e3e8ee;display:flex}.guest-invi-room-head>span[data-v-35a24cc2]{color:#8792a2}.guest-invi-room-head>span[data-v-35a24cc2],.guest-invi-room-single>span[data-v-35a24cc2]{display:block;font-family:Segoe UI;font-size:12.7985px;font-style:normal;font-weight:400;line-height:17px;padding:16px}.guest-invi-room-single>span[data-v-35a24cc2]{color:#000;white-space:pre-line;word-break:break-word}.guest-invi-room-single>span[data-v-35a24cc2]:first-child{font-size:14px;font-weight:600;line-height:19px}@media screen and (max-width:768px){.guest-acc-family-add[data-v-35a24cc2]{width:100%!important}.guest-acc-fam-add-cont[data-v-35a24cc2]{width:100%}.guest-acc-room-container[data-v-35a24cc2]{width:100%!important}.guest-invi-room-head[data-v-35a24cc2]{display:none}.guest-invi-room-single[data-v-35a24cc2]{background:#fff;border:.5px solid #e3e8ee;border-radius:3px;flex-wrap:wrap;justify-content:space-between;margin-bottom:3px}.guest-invi-room-single>span[data-v-35a24cc2]:first-child{width:48%!important}.guest-invi-room-single>span[data-v-35a24cc2]:nth-child(2){text-align:right;width:48%!important}.guest-invi-room-single>span[data-v-35a24cc2]:nth-child(3),.guest-invi-room-single>span[data-v-35a24cc2]:nth-child(4){width:35%!important}.guest-invi-room-single>span[data-v-35a24cc2]:nth-child(5),.guest-invi-room-single>span[data-v-35a24cc2]:nth-child(6){padding-left:0!important;width:15%!important}.guest-invi-room-single>span[data-v-35a24cc2]:nth-child(6){text-align:right}.guest-acc-accept[data-v-35a24cc2]{position:relative;width:100%!important;z-index:99}.guest-acc-accept>.guest-invi-heading[data-v-35a24cc2]{text-align:center!important}.guest-invi-acc-cont>div[data-v-35a24cc2]{position:relative;z-index:999}.guest-invi-acc-cont>img[data-v-35a24cc2]:first-child{z-index:9}}@media screen and (max-width:576px){.guest-acc-room-container .guest-invi-heading[data-v-35a24cc2]{margin-bottom:30px!important;text-align:left!important}}", "",{"version":3,"sources":["webpack://./resources/js/components/guest/invitationDesigns/invitationDefault/InvitationAccommodation.vue"],"names":[],"mappings":"AAoXA,sCAGA,qBAAA,CAFA,iBAAA,CACA,iBAEA,CACA,0CAEA,WAAA,CADA,SAEA,CACA,sDAEA,QAAA,CAGA,YAAA,CAJA,iBAAA,CAEA,OAAA,CACA,WAEA,CACA,mCACA,YAAA,CACA,qBAAA,CACA,sBACA,CACA,qCAOA,UAAA,CANA,oBAAA,CAGA,mBAAA,CADA,eAAA,CAEA,gBAIA,CACA,8EARA,iBAAA,CAMA,kBAAA,CAFA,iBAaA,CATA,yCAMA,UAAA,CALA,mBAAA,CAGA,mBAAA,CADA,eAAA,CAEA,gBAIA,CACA,uCACA,aAAA,CACA,iBACA,CACA,8CAEA,WAAA,CAQA,UAAA,CANA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAPA,YAAA,CAEA,iBAAA,CAMA,iBAEA,CACA,0DACA,kBAAA,CACA,wBAAA,CACA,kBAAA,CACA,iBACA,CACA,2DACA,eAAA,CACA,wBAAA,CACA,kBAAA,CACA,aACA,CACA,iEAEA,kBAAA,CACA,wBAAA,CACA,kBAAA,CAHA,UAIA,CACA,kEACA,eAAA,CAEA,wBAAA,CACA,kBAAA,CAFA,aAGA,CACA,uCAIA,kBAAA,CAHA,YAAA,CACA,qBAAA,CACA,sBAEA,CACA,yCACA,SACA,CACA,sCACA,yBAAA,CACA,iBAAA,CAIA,eACA,CACA,2EAJA,YAAA,CACA,cAAA,CAFA,gBASA,CACA,uCAEA,kBAAA,CACA,iBAAA,CAEA,iBAAA,CAJA,gBAAA,CAGA,sBAAA,CAAA,iBAEA,CACA,uDAMA,UAAA,CALA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAEA,CACA,wDAMA,aAAA,CALA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAEA,CACA,4DACA,kBAAA,CACA,yBAAA,CACA,iBACA,CACA,gEACA,aAAA,CACA,iBACA,CACA,iFACA,oBAAA,CACA,iBACA,CACA,gFAIA,cAAA,CAHA,iBAAA,CACA,OAAA,CACA,OAEA,CACA,mCACA,YAAA,CACA,sBAAA,CACA,aACA,CACA,0CAQA,kBAAA,CACA,wBAAA,CACA,kBAAA,CAHA,UAAA,CAHA,cAAA,CACA,gBAAA,CAMA,iBAAA,CALA,iBAMA,CACA,+EAZA,mBAAA,CACA,iBAAA,CACA,eA0BA,CAhBA,qCAEA,8BAAA,CACA,uBAAA,CASA,UAAA,CAPA,YAAA,CAKA,mBAAA,CAJA,6BAAA,CAKA,gBAAA,CAPA,iBAAA,CAWA,iBAAA,CADA,OAAA,CAbA,oBAAA,CAYA,KAGA,CACA,wCAIA,kBAAA,CAHA,YAAA,CACA,qBAAA,CACA,sBAEA,CACA,4CAEA,WAAA,CADA,UAEA,CACA,+CAYA,qBAAA,CAVA,wBAAA,CACA,kBAAA,CAQA,aAAA,CANA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CARA,YAAA,CAGA,mBAAA,CAMA,iBAAA,CAGA,sBAAA,CAAA,iBACA,CACA,oHAEA,gBACA,CACA,sCACA,iBAAA,CACA,UACA,CACA,iDAYA,qBAAA,CAVA,wBAAA,CACA,kBAAA,CAQA,aAAA,CANA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAKA,gBAAA,CAbA,YAAA,CAGA,mBAAA,CAMA,iBAAA,CAGA,sBAAA,CAAA,iBAEA,CACA,gFAGA,oCAAA,CADA,YAEA,CACA,4CAMA,aAGA,CACA,0FAHA,aAAA,CANA,oBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAGA,YAaA,CAXA,8CAMA,UAAA,CAGA,oBAAA,CACA,qBACA,CACA,0DAEA,cAAA,CADA,eAAA,CAEA,gBACA,CACA,oCACA,uCACA,oBACA,CACA,yCACA,UACA,CACA,2CACA,oBACA,CAIA,uCACA,YACA,CACA,yCACA,eAAA,CACA,yBAAA,CACA,iBAAA,CAEA,cAAA,CACA,6BAAA,CAFA,iBAGA,CACA,0DACA,mBACA,CACA,2DAEA,gBAAA,CADA,mBAEA,CAIA,sHACA,mBACA,CAKA,sHAFA,wBAAA,CADA,mBAOA,CAJA,2DAEA,gBAEA,CAIA,mCAEA,iBAAA,CADA,oBAAA,CAEA,UACA,CACA,uDACA,2BACA,CAEA,0CACA,iBAAA,CACA,WACA,CACA,sDACA,SACA,CACA,CACA,oCACA,+DAEA,4BAAA,CADA,yBAEA,CACA","sourcesContent":["<template>\n    <div\n        class=\"guest-invi-acc-cont\"\n        v-if=\"\n            privileges.guest_invitation.accommodation != 0 &&\n            privileges.guest_invitation.accommodationStatus != 3\n        \"\n    >\n        <img src=\"/assets/guestInvi/acc-right.png\" alt=\"\" />\n        <div\n            v-if=\"privileges.guest_invitation.accommodationStatus == 1\"\n            class=\"guest-acc-accept\"\n        >\n            <h1 class=\"guest-invi-heading\">Accomodation</h1>\n            <h6 class=\"guest-invi-sub-heading\">\n                Accept Invitation to Accept accomodation\n            </h6>\n            <div>\n                <button\n                    @click=\"acceptAcc('accept')\"\n                    :class=\"{\n                        active: privileges.guest_invitation.inviteStatus == 6,\n                    }\"\n                    type=\"button\"\n                    :disabled=\"disableAcceptAcc\"\n                >\n                    Accept\n                </button>\n                <button\n                    @click=\"acceptAcc('decline')\"\n                    :class=\"{\n                        active: privileges.guest_invitation.inviteStatus == 6,\n                    }\"\n                    type=\"button\"\n                    :disabled=\"disableAcceptAcc\"\n                >\n                    Decline\n                </button>\n            </div>\n        </div>\n        <div\n            v-else-if=\"privileges.guest_invitation.accommodationStatus == 2\"\n            class=\"guest-acc-family-add\"\n        >\n            <div style=\"position: relative; width: 100%\">\n                <h1 class=\"guest-invi-heading\">Accomodation</h1>\n                <a\n                    class=\"guest-profile-link\"\n                    :href=\"route('guestprofile.index', user)\"\n                >\n                    <img\n                        v-if=\"screenWidth > 768\"\n                        style=\"margin-right: 10px\"\n                        src=\"/assets/guestInvi/Group-1689.svg\"\n                        alt=\"\"\n                    />\n                    <img\n                        v-else\n                        src=\"/assets/miscellenous/user-plus.svg\"\n                        alt=\"\"\n                    />\n                    <span v-if=\"screenWidth > 768\">Add Members</span>\n                </a>\n            </div>\n            <h6 class=\"guest-invi-sub-heading\">\n                Add Members Detatils to provide accomodation service\n            </h6>\n            <div class=\"guest-acc-fam-add-cont\">\n                <div class=\"guest-acc-fam-input\">\n                    <div v-if=\"hideUser == false\" class=\"guest-acc-fam-single\">\n                        <div>\n                            <span>{{ user.name }}</span>\n                            <img\n                                @click=\"hideUser = true\"\n                                src=\"/assets/guestInvi/close-fam-click.svg\"\n                                alt=\"\"\n                            />\n                        </div>\n                        <div>Me</div>\n                    </div>\n                    <div\n                        v-for=\"item in selectedFam\"\n                        :key=\"item.id\"\n                        class=\"guest-acc-fam-single\"\n                    >\n                        <div>\n                            <span>{{ item.familyname }}</span>\n                            <img\n                                @click=\"addFamily(item, 'remove')\"\n                                src=\"/assets/guestInvi/close-fam-click.svg\"\n                                alt=\"\"\n                            />\n                        </div>\n                        <div>{{ item.familyrelation }}</div>\n                    </div>\n                </div>\n                <div class=\"guest-acc-fam-list\">\n                    <div\n                        @click=\"addFamily(item, 'add')\"\n                        v-for=\"item in familyList\"\n                        :key=\"item.id\"\n                        class=\"guest-acc-fam-single\"\n                    >\n                        <div>{{ item.familyname }}</div>\n                        <div>{{ item.familyrelation }}</div>\n                    </div>\n                    <div\n                        @click=\"hideUser = false\"\n                        v-if=\"hideUser\"\n                        class=\"guest-acc-fam-single\"\n                    >\n                        <div>\n                            <span>{{ user.name }}</span>\n                        </div>\n                        <div>Me</div>\n                    </div>\n                </div>\n                <div class=\"guest-acc-submit\">\n                    <button\n                        :disabled=\"disableAccFamily\"\n                        @click=\"sendAccFamily()\"\n                        type=\"button\"\n                    >\n                        Send Details\n                    </button>\n                </div>\n            </div>\n        </div>\n        <div\n            v-else-if=\"\n                (privileges.guest_invitation.accommodationStatus == 4 ||\n                    privileges.guest_invitation.accommodationStatus == 5) &&\n                privileges.guest_invitation.hostAccommodationStatus == 0\n            \"\n            class=\"guest-acc-family-wait\"\n        >\n            <img src=\"/assets/guestInvi/acc-circle.png\" alt=\"\" />\n            <h1 class=\"guest-invi-heading\">Accomodation</h1>\n            <h6 class=\"guest-invi-sub-heading\">\n                Members Details has been sent. You will be notified when the\n                Accomodation details were allotted to you and your members\n            </h6>\n            <button @click=\"$emit('help')\" type=\"button\">\n                <img src=\"/assets/guestInvi/clarity_help-line.svg\" alt=\"\" />\n                <span>Help</span>\n            </button>\n        </div>\n        <div\n            v-else-if=\"\n                (privileges.guest_invitation.accommodationStatus == 4 ||\n                    privileges.guest_invitation.accommodationStatus == 5) &&\n                privileges.guest_invitation.hostAccommodationStatus == 1\n            \"\n            class=\"guest-acc-room-container\"\n        >\n            <div class=\"guest-acc-room-head\">\n                <h1 class=\"guest-invi-heading\">Accomodation</h1>\n                <div style=\"position: absolute; right: 0; top: 0\">\n                    <button>\n                        <!-- <a :href=\"route('guestacc.export', [user, inviStatus])\"\n                            ><img src=\"/assets/guestInvi/Group-1702.svg\" alt=\"\"\n                        /></a> -->\n                        <img src=\"/assets/guestInvi/Group-1702.svg\" alt=\"\" />\n                    </button>\n                    <button @click=\"$emit('help')\" type=\"button\">\n                        <img\n                            src=\"/assets/guestInvi/clarity_help-line.svg\"\n                            alt=\"\"\n                        />\n                        <span v-if=\"screenWidth > 768\">Help</span>\n                    </button>\n                </div>\n                <div class=\"guest-invi-room-all\">\n                    <div class=\"guest-invi-room-head\">\n                        <span style=\"width: 20%\">Name</span>\n                        <span style=\"width: 20%\">Phone Number</span>\n                        <span style=\"width: 20%\">Hotel</span>\n                        <span style=\"width: 20%\">Block</span>\n                        <span style=\"width: 10%\">Floor</span>\n                        <span style=\"width: 10%\">Room.No</span>\n                    </div>\n                    <div\n                        v-if=\"\n                            privileges.guest_invitation.accommodationStatus == 4\n                        \"\n                        class=\"guest-invi-room-single\"\n                    >\n                        <span style=\"width: 20%\">{{ user.name }}</span>\n                        <span style=\"width: 20%\">{{ user.mobile }}</span>\n                        <span style=\"width: 20%\">{{\n                            guestAccommodationInfo?.hotelName\n                        }}</span>\n                        <span style=\"width: 20%\">{{\n                            guestAccommodationInfo?.block\n                        }}</span>\n                        <span style=\"width: 10%\">{{\n                            guestAccommodationInfo?.floor\n                        }}</span>\n                        <span style=\"width: 10%\">{{\n                            guestAccommodationInfo?.roomno\n                        }}</span>\n                    </div>\n                    <div\n                        v-for=\"(item, index) in accInfo\"\n                        :key=\"index\"\n                        class=\"guest-invi-room-single\"\n                    >\n                        <span style=\"width: 20%\">{{\n                            item.family.familyname\n                        }}</span>\n                        <span style=\"width: 20%\">{{\n                            item.family.familymobile\n                        }}</span>\n                        <span style=\"width: 20%\">{{ item.hotelName }}</span>\n                        <span style=\"width: 20%\">{{ item.block }}</span>\n                        <span style=\"width: 10%\">{{ item.floor }}</span>\n                        <span style=\"width: 10%\">{{ item.roomno }}</span>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <flashMessage :message=\"message\"></flashMessage>\n    </div>\n</template>\n\n<script>\nimport flashMessage from \"../../../FlashMessage.vue\";\n\nexport default {\n    components: {\n        flashMessage,\n    },\n    props: [\n        \"inviStatus\",\n        \"familyAcc\",\n        \"user\",\n        \"accomodationInfo\",\n        \"guestAccommodationInfo\",\n    ],\n    data() {\n        return {\n            privileges: this.inviStatus,\n            familyList: [...this.familyAcc],\n            selectedFam: [],\n            disableAcceptAcc: false,\n            disableAccFamily: false,\n            hideUser: false,\n            accInfo: this.accomodationInfo,\n            message: null,\n            screenWidth: window.screen.width,\n        };\n    },\n    methods: {\n        addFamily(ele, type) {\n            if (type == \"add\") {\n                let index = this.familyList.findIndex((x) => x.id == ele.id);\n                this.familyList.splice(index, 1);\n                this.selectedFam.push(ele);\n            }\n            if (type == \"remove\") {\n                let index = this.selectedFam.findIndex((x) => x.id == ele.id);\n                this.selectedFam.splice(index, 1);\n                this.familyList.push(ele);\n            }\n        },\n        acceptAcc(type) {\n            let _this = this;\n            if (this.privileges.guest_invitation.inviteStatus != 6) {\n                return;\n            }\n            this.disableAcceptAcc = true;\n            let link = route(\"guestprivilege.update\", [\n                _this.user,\n                _this.privileges,\n            ]);\n            let acc = 2;\n            if (type == \"decline\") {\n                acc = 3;\n            }\n            axios({\n                method: \"POST\",\n                url: link,\n                data: { accommodation: acc },\n            })\n                .then((response) => {\n                    console.log(response);\n                    _this.disableAcceptAcc = false;\n                    _this.$emit(\"acc\", response.data);\n                    // _this.privileges = response.data;\n                    if (type == \"accept\") {\n                        _this.message = \"Accomodation Accepted!!\";\n                    }\n                    if (type == \"decline\") {\n                        _this.message = \"Accomodation Declined!!\";\n                    }\n\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                })\n                .catch((error) => {\n                    console.log(error);\n                    _this.disableAcceptAcc = false;\n                    _this.message = \"Something went wrong!!\";\n\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                });\n        },\n        sendAccFamily() {\n            let _this = this;\n            if (this.privileges.guest_invitation.inviteStatus != 6) {\n                return;\n            }\n            if (this.selectedFam.length == 0 && this.hideUser == true) {\n                return;\n            }\n            this.disableAccFamily = true;\n            let link = route(\"guestfamilyprivilege.update\", [\n                _this.user,\n                _this.privileges,\n            ]);\n            let acc = 4;\n            if (_this.hideUser == true) {\n                acc = 5;\n            }\n            axios({\n                method: \"POST\",\n                url: link,\n                data: { accommodation: acc, list: _this.selectedFam },\n            })\n                .then((response) => {\n                    _this.disableAccFamily = false;\n                    _this.$emit(\"acc\", response.data[0]);\n                    _this.accInfo = response.data[1];\n                    // _this.privileges = response.data;\n                    _this.message = \"Accomodation Details Sent!!\";\n\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                })\n                .catch((error) => {\n                    console.log(error);\n                    _this.disableAccFamily = false;\n                    _this.message = \"Something went wrong!!\";\n\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                });\n        },\n        getWidth() {\n            this.screenWidth = window.screen.width;\n        },\n    },\n    watch: {\n        inviStatus: function (newVal, oldVal) {\n            // watch it\n            this.privileges = newVal;\n        },\n    },\n    mounted() {\n        this.$nextTick(() => {\n            window.addEventListener(\"resize\", this.getWidth);\n        });\n    },\n};\n</script>\n\n<style scoped>\n.gue